*{
    margin: 0px;
    padding: 0px;
}

.hjem{
    height: 100vh;
    width: 100vw;
    background: linear-gradient(to bottom, #484848,#9a9a9a,#83a3b3);
}

.navbar{
  background-color: none;
  height: 72px;
  width: 100vw;
  position: absolute;
}
  
  
.logo{
  height: 72px;
  width: 20vw;
  position: relative;
  float: left;
}

.logo:hover{
  cursor: pointer;
}

.logo-svg{
  height: 100%;
  width: 120px;
  float: left;
}
  
.svg-elem-1{
    animation: logoanime 600ms linear 1;
    stroke: #1881CC;
}
  
@keyframes logoanime {
    0%{
      stroke: none;
    }
    50%{
      stroke: none;
    }
    87%{
      stroke: rgba(24, 129, 204, 0.2);
    }
    100%{
      stroke: rgba(24, 129, 204, 1);
    }
}
  
.svg-elem-6{
    stroke: rgba(24, 129, 204, 1);
    animation: logoanime2 200ms linear 1;
    fill:rgba(24, 129, 204, 1);
}
  
@keyframes logoanime2 {
    0%{
      stroke: none;
      fill: none;
    }
    50%{
      stroke: rgba(24, 129, 204, 0.2);
      fill: rgba(24, 129, 204, 0.2);
    }
    100%{
      stroke: rgba(24, 129, 204, 1);
      fill: rgba(24, 129, 204, 1);
    }
}
  
.svg-elem-7{
    stroke: rgba(24, 129, 204, 1);
    animation: logoanime3 400ms linear 1;
    fill:rgba(24, 129, 204, 1);
}
  
@keyframes logoanime3 {
    0%{
      stroke: none;
      fill: none;
    }
    50%{
      stroke: none;
      fill: none;
    }
    75%{
      stroke: rgba(24, 129, 204, 0.2);
      fill: rgba(24, 129, 204, 0.2);
    }
    100%{
      stroke: rgba(24, 129, 204, 1);
      fill: rgba(24, 129, 204, 1);
    }
}
  
.navbar-right{
    width: 80vw;
    height: 100%;
    position: relative;
    float: right;
}
  
.navlist{
    width: 40vw;
    height: 100%;
    position: relative;
    float: left;
    margin-left: 10vw;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}
  
.navelem{
    grid-column: 1fr;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
  
.hover-sec{
  display: none;
}
  
.hover-sec:hover{
    cursor:auto;
}

.courses:hover .hover-sec, .students:hover .hover-sec, .tutors:hover .hover-sec{
  animation: hover-sec-ann 200ms 1 ;
  display: grid;
  position: absolute;
  background-color: #5c6569;
  top: 72px;
  width: 31vw;
  height: 150px;
  border-radius: 10px;
}

@keyframes hover-sec-ann {
  0%{
    background-color: #5c656900;
  }
  50%{
    background-color: #5c656934;
  }
  100%{
    background-color: #5c6569;
  }
}

.courses:hover .hover-sec-courses{
  grid-template-columns:repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
}

.students:hover .hover-sec-students{
  grid-template-columns:repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
}

.tutors:hover .hover-sec-tutors{
  grid-template-rows: repeat(3, 1fr);
}

.courses-elem{
  display: flex;
  align-items: center;
  justify-content: center;
}

.students-elem{
  display: flex;
  align-items: center;
  justify-content: center;
}

.tutors-elem{
  display: flex;
  align-items: center;
  justify-content: center;
}

.courses-elem-text, .students-elem-text, .tutors-elem-text{
  color: #b5d6df;
  animation: change-color6 800ms 1;
  font-size: 0.98em;
  font-weight: 500;
  user-select: none;
}

@keyframes change-color6 {
  0%{
    color:#b5d6df00;
  }
  100%{
    color:#b5d6df
  }
}

.courses-elem:hover, .students-elem:hover, .tutors-elem:hover{
  cursor: pointer;
}

.courses-elem:hover .courses-elem-text, .students-elem:hover .students-elem-text, .tutors-elem:hover .tutors-elem-text{
  color: #484848;
  animation: change-color5 150ms 1;
}

@keyframes change-color5 {
  0%{
    color:#b5d6df;
  }
  100%{
    color:#484848
  }
}


.courses:hover .hover-sec{
  left: 4.5vw;
}

.tutors:hover .hover-sec{
  right: 4.5vw;
}

.hover-rect-svg{
  display: none;
}

.courses:hover .hover-rect-svg, .students:hover .hover-rect-svg, .tutors:hover .hover-rect-svg{
  display: block;
  position: absolute;
  top: 45px;
  fill: #5c6569;
}

.hover-rect{
  animation: hover-rect-ann 200ms 1;
  fill:#5c6569 ;
}

@keyframes hover-rect-ann {
  0%{
    fill: #5c656900;
  }
  50%{
    fill: #5c656934;
  }
  100%{
    fill: #5c6569;
  }
}

  
.nav-text{
    color: #b5d6df;
    font-size: 1.1em;
    font-weight: 500;
    user-select: none;
}
  
@keyframes change-color {
    0%{
      color:#b5d6df;
    }
    100%{
      color:#5c6569
    }
}
  
.navelem:hover .nav-text{
    color: #5c6569;
    animation: change-color 150ms 1;
}
  
.login-signup-btn-nav{
    width: 30vw;
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
  
.login-signup-btn-nav-box{
    width: 100px;
    height: 50%;
    background-color: #3f82b1;
    border-radius: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    float: right;
    margin-right: 5px;
}
  
.login-signup-btn-nav-text{
    color: #c6e7f1;
    font-size: 0.9em;
    font-weight: 450;
    user-select: none;
}

@keyframes change-size {
    0%{
      font-size: 0.9em;
      font-weight: 450;
    }
    100%{
      font-size: 0.95em;
      font-weight: 470;
    }
}
  
.login-signup-btn-nav-box:hover .login-signup-btn-nav-text{
    font-size: 0.95em;
    font-weight: 470;
    animation: change-size 200ms 1;
    user-select: none;
}
  
@keyframes change-color2 {
    0%{
      background-color:#3f82b1;
    }
    100%{
      background-color:#3f81b147;
    }
}

.login-signup-btn-nav-box:hover{
    background-color:#3f81b100;
    cursor: pointer;
    animation: change-color2 200ms 1;
}
  
.search-sec{
    height: 100vh;
    width: 100vw;
}

.searchbar{
    height: 35px;
    width: 40vw;
    background-color:#5c6569;
    border-radius: 20px;
    border-color:#506771;
    border-width: 2px;
    border-style: solid;
    position: absolute;
    top: 320px;
    left: 30vw;
    color: #b5d6df;
    outline-width: 0px;
    text-align: center;
    font-size: 1.1em;
    font-weight: 500;
}

.searchbar::placeholder{
    color: #b5d6df;
    font-size: 0.8em;
    font-weight: 400;
    user-select: none;
}

.searchbar:hover{
    animation: searchhover 100ms 1;
    border-color:#668491;
    border-width: 2px;
}
  
@keyframes searchhover {
    0%{
      border-color:#506771;
    }
    100%{
      border-color:#668491;
      border-width: 2px;
    }
}
  
.searchbar:hover::placeholder{
    color: #a4c3cb;
}
  
.searchbar:focus{
    color: #b5d6df;
    font-size: 1.1em;
    font-weight: 500;
}
  
.search-btn{
    height: 32px;
    width: 31px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 323px;
    left: calc(70vw - 29px);
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    border-top-width: 0px;
    border-right-width: 0px;
    border-bottom-width: 0px;
    background-color: #5c6569;
    border-left-width: 2px;
    border-left-color: #506771;
    border-left-style: solid;
}
  
.search-btn:hover{
    background-color: #535f64;
    cursor: pointer;
}
  
.search-svg{
    height: 20px;
    width: 20px;
}
  
.search-svg-circle, .search-svg-line{
    stroke: #b5d6df ;
  }